export default {
	data() {
		return {
			newsList: []
		}
	},
	onLoad() {
		this.getNews()
	},
	methods: {
		async getNews () {
			const res = await this.$myRequest({
				url: '/api/getnewslist'
			})
			this.newsList = res.data.message
		},
		getDetail (id) {
			uni.navigateTo({
				url: '../news-detail/news-detail?id='+id
			})
		},
		goDetail (id) {
			this.getDetail(id)
		}
	}
}
